<template>
<div>
    <Head title="我的收货地址" />
   <div v-if="len">
       <van-button type="default" size="large" style="margin-top:300px;" @click="add()">
           暂无地址，立即去新增</van-button>
  </div>

  <div v-else>
      <van-address-list
  v-model="chosenAddressId"
  :list="list"
  @add="onAdd"
  @edit="onEdit"
   >
      </van-address-list>
  </div>
  





</div>
</template>



<script>


export default {
    data(){
        return {
           
       chosenAddressId: '1',
      list: [],
      len:''
      
        }
    },
    created(){
       var username=window.localStorage.getItem("users");
      this.$axios.post("/vue/getAddress",{username}).then(res=>{
      //  window.console.log(res)
        if(res.data.code==200){
          this.list=res.data.addressList;
           this.len=false;
         
        }else{
           this.len=true;
          // Toast("您还没有收货地址")
        }
      })

    },
    methods:{
        onAdd(){
          this.$router.push({name:"onadd"})
        },
        add(){
 this.$router.push({name:"onadd"})
        },
        onEdit(){

        }

        

    }
    
}
</script>


